/* 0 - 965px */
@media (max-width: 965px) {
  .container {
    max-width: 100%;
  }
  #drum-pad {
    height: auto;
  }
  .button {
    height: 10%;
  }
  #display {
    width: 100%;
  }
  #controls {
    width: 1em;
    display: flex;
  }
  .wrapper {
    display: flex;
    margin: 1em;
  }
}
/* 965px+ */
@media (min-width: 965px) {
  .container {
    max-width: 70em;
  }
  #drum-pad  {
    height: 50em;
  }
  .button {
    height: 30%;
  }
  #display {
    margin: 15px auto;
    padding: 15px;
  }
  .wrapper {
    display: flex;
    height: 100vh;
  }
}

body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
}

.wrapper {
  height: 100vh;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
  border: 1px solid black;
}

.button {
  display: flex;
  border: 1px solid pink;
  width: 30%;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#drum-pad {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 50em;
  align-items: center;
  
}

#controls {
  display: inline-block;
  flex-wrap: wrap;
  margin: auto;
  text-align: center;
  width: 10em;
}

#display {
  text-align: center;
}

/* Toggle Switch CSS from here: https://www.w3schools.com/howto/howto_css_switch.asp */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
/* END Toggle Switch CSS */